import React,{useState} from 'react'
import {Link , Redirect } from "react-router-dom";

import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';   
import { isAutheticated } from './login/authenticate';
import { signout } from './logout';
import { API } from './Backend';
toast.configure() 



// Hitting the API
const AddStudent = () => {

  const { user, token } = isAutheticated();

  // API for Registering
  const register = userdata => {
    return fetch(`${API}/student/create/${user._id}`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`
      },
      body: JSON.stringify(userdata)
    })
      .then(response => {
        return response.json();
      })
      .catch(err => console.log(err));
  };

  // adding values to register

  const [values, setValues] = useState({
    name: "",
    class_name:"",
    batch: "",
    roll_no:"",
    error:false,
    success:false
  })
  const {name, class_name, batch, roll_no,error, success  } =  values 

  // Submit the API
  const onSubmit = event => {
    event.preventDefault();
    register( {name, class_name, batch,roll_no} )
      .then(data => {
          setValues({...values,name:"",class_name:"",batch:"",roll_no:""})
          if(data.error){
            console.log("Registration  Error")
            toast("Registration Error")
          }
          else{
            console.log("Registered SuccessFully")
            toast("Registered SuccessFully, Please wait.....")
          }
      })
      .catch(err=>{
        setValues({...values,name:"",class_name:"",batch:"",roll_no:""})
        console.log("Registration Error") 
        toast("Server Error")
      })
  };

  // CHanging the values
  const handleChange = name => event => {
    event.preventDefault();
    setValues({ ...values, [name]: event.target.value });
  };


  // Returning Part
  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="9" lg="7" xl="6">
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <CForm>
                <div className="row">
                    <div className="col-6">
                    <h3>Admin Dashboard</h3>
                  </div>
                  <div className="col-6 text-right">
                    <Link to="/">
                  <button  type="button" class="btn btn-danger" onClick={signout} style={{height:"36px"}}> Logout Here..</button>
                  </Link>
                  </div>
                  </div>
                
                  <p className="text-muted">Create student record here...</p>
                  <br/>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-user" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Name" autoComplete="username"   onChange={handleChange("name")} value={name}/>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>@</CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Roll No Eg. 189086" autoComplete="email"  onChange={handleChange("roll_no")}  value={roll_no}/>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>@</CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Batch Eg. 2018-22" autoComplete="email"  onChange={handleChange("batch")}  value={batch}/>
                  </CInputGroup>

                  
                  

                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                       @
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Class Name Eg. First" autoComplete="new-password"  onChange={handleChange("class_name")} value={class_name} />
                  </CInputGroup>
                  <CInputGroup className="mb-4">
                    <CInputGroupPrepend>
                    </CInputGroupPrepend>
                  </CInputGroup>
                  
                <CButton type="button" color="success" className="mt-3" onClick={onSubmit}   block >Create Student Account</CButton>
                
                  </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
      <ToastContainer  />
    </div>
  )
}

export default AddStudent

